export default {
	data() {
		return {
			indexActiveImg: this.$indexActiveImg,
			videoImg: this.$videoImg,
			myImg: this.$myImg,
			downAppImg: this.$downAppImg,

			displayNone: false,
			topMenuPosition: true,
			refreshing: false,
		}
	},
	methods: {
		gotoLogin() {
			if (this.token) {
				uni.redirectTo({
					url: '../../pages/center/center'
				})
			} else {
				uni.redirectTo({
					url: '../../pages/login/login'
				})
			}
		},
		goRecommend() {
			uni.redirectTo({
				url: '../../pages/new/recommend'
			})
		},
		goFollow() {
			uni.redirectTo({
				url: '../../pages/new/new'
			})
		},
		gotoVideo() {
			uni.redirectTo({
				url: '../../pages/video/video?url=/new/post'
			})
		}
	},
}
